import React from 'react'
import {Link} from 'react-router-dom'
import {changeNum} from '../action/login'
const Chk_rule = props =>{
  const dispatch = props.path
  return(
    <div className='chk_rule'>
      <div className='content'>
        <div className="logo"></div>
        <div className ='rulesBody'>
          <div className="rulesTile"></div>
          <div className="formDIV">
            <input name="submit" type="button" className="za_button" value="" onClick={()=>{
              dispatch(changeNum(0)) 
              sessionStorage.removeItem('sessionNum');
              sessionStorage.removeItem('sessionNmae');
              }} tabIndex="2"></input>
            <input name="submit" type="button" className="za_button" value="" onClick={()=>{
              dispatch(changeNum(2))
              sessionStorage.setItem('sessionNum', 2);
              }} tabIndex="1" ></input>
          </div>
          <table border="0" align="center" cellPadding="0" cellSpacing="0">
            <tbody><tr>
                <td width="100" className="title-style3"><div align="center"><Link to='/'></Link></div></td>
                <td width="100" className="title-style3"><div align="center" id="rule_ipl_link"><Link to='/'></Link></div></td>
                <td width="100" height="30" className="title-style3"><div align="center"><Link to='/'></Link></div></td>
                <td width="100" height="30" className="title-style3"><div align="center"><Link to='/'></Link></div></td>
              </tr>
            </tbody>
          </table>
          <RuleBody />
        </div>
      </div>
    </div>
  )
}
const RuleBody = props =>{
  return(
    <div id="rule_all" >
			<table width="655" border="0" align="center" cellPadding="0" cellSpacing="0">
				<tbody>
          <tr>
            <td height="30" className="title-style2">
              <div align="center"><p align="center"></p></div>
            </td>
          </tr>
          <tr>
            <td>
              <ol>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1"></li>
                <li className="title-style1">&lt;&gt;</li>
                <li className="title-style1">/ (:)</li>
                <li className="title-style1"></li>
              </ol>    
            </td>
          </tr>
          <tr>
            <td height="30"><div align="right"><Link to='/' className="title-style3">TOP</Link></div></td>
          </tr>
        </tbody>
      </table>
		</div>
  )
}
export default Chk_rule