import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import Submenu from './submenu'

const menu = [
  { text: '', url: '/', sub: false },
  {
    text: '',
    url: '/sport',
    sub: true,
    submenu: [{ txt: '' }, { txt: '' }, { txt: '' }],
  },
  {
    text: '',
    url: '/live',
    sub: true,
    submenu: [
      { txt: 'AG' },
      { txt: 'BB' },
      { txt: 'MG' },
      { txt: 'HG' },
      { txt: 'OG' },
      { txt: 'WM' },
      { txt: 'BG' },
      { txt: 'DS' },
      { txt: '' },
    ],
  },
  {
    text: '',
    url: '/dianzi',
    sub: true,
    submenu: [
      { txt: 'BG' },
      { txt: 'AG' },
      { txt: 'BBIN' },
      { txt: 'BG' },
      { txt: 'YOPLAY' },
      { txt: 'XIN' },
      { txt: 'MG' },
      { txt: 'CQ9' },
    ],
  },
  { text: '', url: '/qipai', sub: true, submenu: [{ txt: '' }, { txt: '' }] },
  {
    text: '',
    url: '/lottery',
    sub: true,
    submenu: [
      { txt: '' },
      { txt: 'PK' },
      { txt: '' },
      { txt: '' },
      { txt: '' },
      { txt: '' },
      { txt: '' },
      { txt: '' },
    ],
  },
  { text: '', url: '/six', sub: true, submenu: [{ txt: '' }, { txt: '' }] },
  { text: '', url: '/hot', sub: false },
]

const Header = props => {
  return (
    <div className='header'>
      <div className='headerWarp'>
        <Link to='/'>
          <div className='logo'></div>
        </Link>
        <div className='top'>
          <ul className='topmenu'>
            <li>
              <Link to='/'></Link>
            </li>
            <li>
              <Link to='/'></Link>
            </li>
            <li>
              <Link to='/'></Link>
            </li>
            <div className='topinner'>
              <img src={require('../static/images/gq1.jpg')} alt='' />
              <img src={require('../static/images/gq2.jpg')} alt='' />
              <img src={require('../static/images/gq3.jpg')} alt='' />
            </div>
          </ul>
          <ul className='menu'>
            {menu.map(item => {
              let sub = item.sub
              return (
                <li className='menuLi' key={item.text}>
                  <NavLink exact activeClassName='on' to={item.url}>
                    {item.text}
                  </NavLink>
                  {sub ? <Submenu text={item.submenu} /> : ''}
                </li>
              )
            })}
          </ul>
        </div>
      </div>
    </div>
  )
}

export default Header
