import React from 'react'
import { Link } from 'react-router-dom'

const Footer = props => {
  return (
    <div className='footer'>
      <div className='footWarp'>
        <Link to='/' className='hyllq'>
          <img src={require('../static/images/ublogo.png')} alt='' />
        </Link>
        <div className='footMenu'>
          <ul>
            <li>
              <Link to='/about'></Link> |
            </li>
            <li>
              <Link to='/about/contactUs'></Link> |
            </li>
            <li>
              <Link to='/about/agent'></Link> |
            </li>
            <li>
              <Link to='/about/deposit'></Link> |
            </li>
            <li>
              <Link to='/about/withdraw'></Link> |
            </li>
            <li>
              <Link to='/about/partner'></Link> |
            </li>
            <li>
              <Link to='/about/novice'></Link> 
            </li>
            {/* <li>
              <Link to='/about'></Link>
            </li> */}
          </ul>
          <div className='copy'>Copyright   Reserved</div>
        </div>
      </div>
    </div>
  )
}

export default Footer
